.contactContainer {
	padding: 100px 50px 0;
}

.contactContainer .formContainer {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	width: 550px;
	margin: 0 auto;
	padding: 36px;
	background-color: rgba(255, 255, 255, 0.4);
	-webkit-box-shadow: 3px 3px 20px 2px rgba(0, 0, 0, 0.2);
	        box-shadow: 3px 3px 20px 2px rgba(0, 0, 0, 0.2);
	border-radius: 8px;
	overflow: hidden;
	text-align: center;
}

.contactContainer .formContainer .title {
	text-align: center;
	padding: 10px;
	margin: 16px 16px 26px;
	color: #fff;
	letter-spacing: 4px;
	font-size: 22px;
	text-transform: uppercase;
	font-weight: bold;
}

.contactContainer .formContainer .inputText {
	width: 100%;
	font-size: 16px;
	color: #fff;
	letter-spacing: 1.2px;
	padding: 16px 0;
	background-color: rgba(0, 0, 0, 0);
	margin: 10px 0;
	border: none;
	border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}

.contactContainer .formContainer .inputText::-webkit-input-placeholder {
	color: #fff;
}

.contactContainer .formContainer .inputText:-ms-input-placeholder {
	color: #fff;
}

.contactContainer .formContainer .inputText::-ms-input-placeholder {
	color: #fff;
}

.contactContainer .formContainer .inputText::placeholder {
	color: #fff;
}

.contactContainer .formContainer .inputSubmit:focus,
.contactContainer .formContainer .inputText:focus {
	outline: none;
}

.contactContainer .formContainer .inputSubmit {
	background-color: rgba(255, 255, 255, 0.3);
	border: none;
	border-radius: 10000px;
	padding: 17px 36px;
	margin-top: 26px;
	color: #fff;
	font-size: 18px;
	cursor: pointer;
	-webkit-box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.06);
	        box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.06);
	-webkit-transition: all .5s ease;
	-o-transition: all .5s ease;
	transition: all .5s ease;
}

.contactContainer .formContainer .inputSubmit:hover {
	-webkit-box-shadow: 0 0 25px 2px rgba(0, 0, 0, 0.19);
	        box-shadow: 0 0 25px 2px rgba(0, 0, 0, 0.19);
}

.contactContainer .footer {
	position: fixed;
	bottom: 0;
	left: 0;
	right: 0;
	text-align: center;
	padding: 20px;
	color: #ddd;
	text-transform: capitalize;
}



