.homeContainer {
    padding: 100px 50px 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    height: 100vh;
}

.leftContent {
    display: block;
    width: 65%;
    height: 100%;
}

.leftContent .mainImage {
    height: 100%;
    width: 100%;
    background-image: url("../../assets/img/home_main_img.png");
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
}

.leftContent .mainBgImage {
    position: fixed;
    top: 0;
    left: 20%;
    width: 400px;
    height: 100vh;
    background-image: url("../../assets/img/home_bg_img.png");
    background-size: cover;
    background-repeat: no-repeat;
    z-index: -1;
}

.rightContent {
    max-width: 500px;
    text-align: right;
    font-weight: lighter;
}

.rightContent .title {
    font-size: 72px;
    color: #B0AAFF;
    text-transform: uppercase;
}

.rightContent .description {
    font-size: 20px;
    color: #E2E0FF;
    margin-top: 50px;
    line-height: 1.4;
    letter-spacing: 1.5px;
}



